import {
  ActionConstraint,
  ControllerMessenger,
  EventConstraint,
} from '@metamask/controllers';
import { JsonRpcRequest } from '@metamask/utils';
import nanoid from 'nanoid';
import { getJsonRpcRequest } from './utils';

// Borrowed from https://github.com/MetaMask/controllers/blob/main/src/ControllerMessenger.ts
type ExtractActionParameters<Action, T> = Action extends {
  type: T;
  handler: (...args: infer H) => any;
}
  ? H
  : never;
type ExtractActionResponse<Action, T> = Action extends {
  type: T;
  handler: (...args: any) => infer H;
}
  ? H
  : never;

type ExtractEventPayload<Event, T> = Event extends { type: T; payload: infer P }
  ? P
  : never;

export class ProxyableControllerMessenger<
  Action extends ActionConstraint,
  Event extends EventConstraint
  > extends ControllerMessenger<Action, Event> {
  private proxyHandler: (request: JsonRpcRequest<unknown>) => Promise<unknown>;

  private proxyableActions: Action['type'][];

  constructor(
    proxyHandler: (request: JsonRpcRequest<unknown>) => Promise<unknown>,
    proxyableActions: Action['type'][],
  ) {
    super();
    this.proxyHandler = proxyHandler;
    this.proxyableActions = proxyableActions;
  }

  call<T extends Action['type']>(
    actionType: T,
    ...params: ExtractActionParameters<Action, T>
  ): ExtractActionResponse<Action, T> {
    if (this.proxyableActions.includes(actionType)) {
      // @ts-expect-error Result type should be a promise
      return this.proxiedCall(actionType, ...params);
    }

    return super.call(actionType, ...params);
  }

  private async proxiedCall<T extends Action['type']>(
    actionType: T,
    ...p: ExtractActionParameters<Action, T>
  ): Promise<ExtractActionResponse<Action, T>> {
    // Guaranteed to be defined in proxyableActions
    const id = nanoid();
    // @ts-expect-error Result type should be a promise
    return this.proxyHandler(
      getJsonRpcRequest(id, 'ControllerMessenger:proxyCall', [
        actionType,
        p,
      ]),
    );
  }

  publish<E extends Event['type']>(
    eventType: E,
    ...payload: ExtractEventPayload<Event, E>
  ) {
    // Always publish events across proxy for now
    this.handleProxyPublish(eventType, ...payload);
    super.publish(eventType, ...payload);
  }

  private handleProxyPublish<E extends Event['type']>(
    eventType: E,
    ...payload: ExtractEventPayload<Event, E>
  ) {
    const id = nanoid();
    this.proxyHandler(
      getJsonRpcRequest(id, 'ControllerMessenger:proxyEvent', [
        eventType,
        payload,
      ]),
    );
  }

  // Handles proxy event from other client
  handleProxyEvent(request: JsonRpcRequest<[Event['type'], unknown[]]>) {
    const [eventType, payload] = request.params!;
    // TODO: Validate and type
    super.publish(eventType, ...payload as any);
  }

  // Handles proxy call from other client
  handleProxyCall(request: JsonRpcRequest<[Action['type'], unknown[]]>) {
    const [actionType, payload] = request.params!;
    // TODO: Validate and type
    return super.call(actionType, ...payload as any);
  }
}
